import Vue from 'vue'
export default (function (axios) {
    return {
        pagepation: "",     //翻页
        reportreceive: function (param,callback, error) {      //滚动列表加载
            let params = param;
            if(param){
                params = Object.assign(params, this.pagepation)
            }else{
                params = this.pagepation
            }
            axios.get("/report-publishing/receiving", { params: params})
            .then(res => { callback(res) })
        },
        reportpublic: function (param,callback, error) {      //滚动列表加载
            let params = param;
            if(param){
                params = Object.assign(params, this.pagepation)
            }else{
                params = this.pagepation
            }
            axios.get("/report-publishing/publishing", { params: params})
            .then(res => { callback(res) })
        },
        staticpublic: function (param,callback, error) {      //滚动列表加载
            let params = param;
            axios.get("/report-publishing/publishing/statistics", { params: params})
            .then(res => { callback(res) })
        },
        staticreceive: function (param,callback, error) {      //滚动列表加载
            let params = param;
            axios.get("/report-publishing/receiving/statistics", { params: params})
            .then(res => { callback(res) })
        }
    }
})(new Vue().axios)



